<template>
    <transition name="fade">
        <div class="home" :style="homeStyle">
            <!--左侧导航-->
            <div class="home_navigation" v-if="!(phone || this.$store.state.phone || this.$store.state.screenWidth<500)">
                <!--背景-->
                <div style="position: absolute;width:100%;height:100%;overflow: hidden;" >
                    <img src="../assets/background_cloud.gif"  :style="navigationBGImageStyle">
                </div>
                <div style="background-color: rgba(70, 70, 70, 0.6);width:100%;height:100%;position: absolute;"></div>
                <!--左上角角标-->
                <div style="position: absolute;width:30px;height:30px;">
                    <img src="../assets/left_top_triangle.png" @click="backToLaunch" style="width:100%;height:100%;cursor: pointer;">
                </div>
                <!--个人信息-->
                <div style="padding-top:50px">
                    <div class="avatar_border">
                        <div class="avatar_view">
                            <img src="../assets/avatar.jpeg">
                        </div>
                    </div>
                    <div style="text-align:center;align-items:center;position: relative;">
                        <p style="color:white;font-size: 18px;">林佳</p>
                    </div>
                    <div style="width:200px;height:1px;background-color: white;left:50%;margin-left:-100px;position: relative;"></div>
                    <div class="my_info_view">
                        <ul>
                            <li>
                                <a href="https://github.com/ShmilyLin" target="_blank" v-on:click.stop><img src="../assets/github.png"></a>
                            </li>
                            <li>
                                <a href="http://www.jianshu.com/u/4a549ca9b701" target="_blank" v-on:click.stop><img src="../assets/jianshu.png"></a>
                            </li>
                            <li>
                                <a href="Mailto:522402543@qq.com" target="_blank" v-on:click.stop><img src="../assets/email.png"></a>
                            </li>
                        </ul>
                    </div>
                    <div style="width:150px;height:1px;background-color: white;left:50%;margin-left:-75px;position: relative;margin-top:15px;"></div>
                </div>
                <!--目录-->
                <div class="catalog_div">
                    <ul>
                        <li>
                            <p @click="myResumeClicked">· 我的简历 ·</p>
                        </li>
                    </ul>
                </div>
                <div class="catalog_div" :style="navigationCatalogStyle" style="padding-top:30px">
                    <div style="width:265px;height:100%;overflow-y:scroll;overflow-x:hidden">
                        <ul v-if="!showArticle">
                            <li v-for="albumItem in this.$store.state.listData.blog">
                                <p @click="selectedAlbum(albumItem)">{{albumItem.albumName}}</p>
                            </li>
                        </ul>
                        <ul v-if="showArticle">
                            <li v-for="articleItem in this.currentAlbum.articles">
                                <p @click="selectedArticle(articleItem)">{{articleItem.articleName}}</p>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <!--手机端导航-->
            <div class="home_navigation_phone" v-else>
                <div style="width:38px;height:38px;border-radius:20px;left: 10px;top:5px;position: relative;border:1px solid white;background-color:white;float:left">
                    <div style="width:34px;height:34px;border-radius:18px;margin-left:1px;margin-top:1px;border:1px solid white;overflow:hidden;" @click="backToLaunch">
                        <img src="../assets/avatar.jpeg" style="width:100%;height:100%">
                    </div>
                </div>
                <div style="position:relative;float:left;margin-left:18px">
                    <p style="color:white;font-size:18px;">林佳</p>
                </div>
                <div style="position:relative;float:right;margin-right:10px;margin-top:10px" @click="phoneCatalogClicked">
                    <img src="../assets/catalog_phone.png" style="width:30px;height:30px">
                </div>
            </div>
            <!--右侧路由页面-->
            <div class="router_view" :style="routerViewStyle">
                <div :style="routerViewContentStyle">
                    <router-view></router-view>
                    <div class="bottom_illustrate_view">
                        <div style="width:200px;height:1px;background-color: rgb(150, 150, 150);position: relative;"></div>
                        <p>本站由 <router-link to="/resume">林佳</router-link> 创建，采用 <a href="https://cn.vuejs.org/" target="_blank">Vue</a> 框架编写，您可以在 <a href="" target="_blank">GitHub</a> 找到本站源码 - © 2017</p>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'home',
    data () {
        var phone = false;
        var userAgentInfo = navigator.userAgent;
		var Agents = ["Android", "iPhone",
					"SymbianOS", "Windows Phone",
					"iPad", "iPod"];
		for (var v = 0; v < Agents.length; v++) {
			if (userAgentInfo.indexOf(Agents[v]) > 0) {
				phone = true;
				break;
			}
		}
        return {
            phone: phone,
            showAlbumArticlesList: false, // 展示文集中的文章列表
            showArticle: false, // 展示文章
        }
    },
    computed: {
        homeStyle: function () {
            if (!(this.$store.state.phone || this.$store.state.screenWidth<500)) {
                return {
                    'min-width': '1000px'
                }
            }
        },
        navigationBGImageStyle: function () {
            return {
                'height': this.$store.state.screenHeight + 'px',
                'width': ((this.$store.state.screenWidth * 575)/400) + 'px',
                'margin-left': '-' + ((((this.$store.state.screenHeight * 575)/400) - 250)/2) + 'px'
            }
        },
        navigationCatalogStyle: function () {
            return {
                'height': (this.$store.state.screenHeight - 350) + 'px'
            }
        },
        routerViewStyle: function () {
            if (this.$store.state.screenWidth < 500 || this.$store.state.phone) {
                return {
                    'width': (this.$store.state.screenWidth-40) + 'px',
                    'padding-top': '20px',
                    'overflow': 'auto',
                    'padding-left': '20px',
                    'padding-right': '20px',
                }
            }
            return {
                'float': 'left',
                'width': '700px',
                'padding-left': '50px',
            }
        },
        routerViewContentStyle : function () {
            if (!(this.$store.state.phone || this.$store.state.screenWidth<500)) {
                return {
                    'height': '100%',
                    'width': '715px',
                    'overflow-y': 'auto',
                }
            }
            return {
                // 'height': '100%',
                'width': '100%',
            };
        }
    },
    methods: {
        myResumeClicked: function () {
            this.$router.push('/resume');
        },
        // 返回Launch页面
        backToLaunch: function() {
            this.$router.replace('/');
        },
        // 选择文集
        selectedAlbum: function (album) {
            this.$router.push('/home/album/' + album.albumIndex);
            this.showAlbumArticlesList = true;
            this.$store.commit('setCurrentAlbum', album);
        },
        // 选择文章
        selectedArticle: function (article) {
            this.showArticle = true;
            this.$store.commit('setCurrentArticle', article);
        },
        // 手机模式下点击菜单
        phoneCatalogClicked: function () {

        },
    }
}
</script>

<!-- // 添加scoped，代表限制这个style标签内的CSS属性只在当前的组件中可用 -->
<style scoped>
/*引入其他css文件*/
/*@import './component/css/easydialog.min.css';*/
.home {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}
.home_navigation {
    width: 250px;
    height: 100%;
    background-color: rgb(55, 55, 55);
    /*background: rgb(55, 55, 55) url(../assets/background_cloud.gif) no-repeat center;
    background-size: 1150px 800px;*/
    float: left;
    position: relative;
    overflow: hidden;
}
.home_navigation_phone {
    width: 100%;
    position: fixed;
    height: 50px;
    background-color: rgb(55, 55, 55);
}
.blur {	
	filter:blur(10px);
	-webkit-filter:blur(10px);
	-moz-filter:blur(10px);
	-ms-filter:blur(10px);
	-o-filter:blur(10px);
}
/*头像*/
.avatar_border {
	position: relative;
	width:66px;
	height:66px;
	border-radius:33px;
	border:1px solid white;
	background-color:white;
	left: 50%;
	margin-left: -33px;
	
}
.avatar_view {
	width:60px;
	height:60px;
	border-radius:30px;
	border:1px solid white;
	overflow:hidden;
	margin-left:2px;
	margin-top:2px;
}
.avatar_view img {
	width:100%;
	height:100%;
}

.my_info_view {
    text-align:center;
    align-items:center;
	width:150px;
	position: relative;
	left: 50%;
	margin-left: -75px;
	overflow: hidden;
	margin-top: 20px;
}

.my_info_view ul {
    height: 100%;
}
.my_info_view li {
	float: left;
	width: 33%;
	height: 20px;
	overflow: hidden;
	list-style-type:none;
}

.my_info_view li img {
	width:20px;
	height:20px;
}


/*目录*/
.catalog_div {
    width: 100%;
    color: white;
    text-align: center;
    align-items: center;
    overflow: hidden;
}

.catalog_div li {
    position: relative;
    width: 150px;
    left: 50%;
    margin-left: -75px;
	height: 30px;
    overflow: hidden;
	list-style-type:none;
    border-bottom: 1px solid white;
}
.catalog_div p {
    padding: 0;
    padding-top: 7px;
    margin: 0;
    cursor: pointer;
}

/*路由页面*/

.router_view {
    height: 100%;
    overflow: hidden;
}

/*底部说明*/

.bottom_illustrate_view {
    font-size:12px;
    color:rgb(150, 150, 150);
    padding-bottom: 20px;
}

.bottom_illustrate_view p {
    line-height: 20px;
}

.bottom_illustrate_view a:link {
    color:rgb(55, 55, 55);
}

.bottom_illustrate_view a:visited {
    color:rgb(150, 150, 150);
}
.bottom_illustrate_view a:hover {
    color:rgb(55, 55, 55);
}
.bottom_illustrate_view a:active {
    color:rgb(55, 55, 55);
}
</style>


