<template>
    <div class="articlesList">
        <!--文章列表-->
        <ul>
            <li v-for="(articleItem, index) in this.$store.state.currentPageArticles" :style="articleListLiStyle">
                <p style="margin-bottom:15px;cursor: pointer;" :style="headerTitleStyle"><b>{{articleItem.articleName}}</b></p>
                <p style="font-size:12px;color:rgb(170, 170, 170);padding:0;margin:0">{{articleItem.creatTime}} · <span @click="jumpToAlbum(articleItem)" style="cursor: pointer;">{{articleItem.albumName}}</span></p>
                <p style="font-size:15px;line-height: 22px;color: rgb(120, 120, 120);padding:0;margin-top:5px;margin-bottom:10px;cursor: pointer;">{{articleItem.introduction}}</p>
            </li>
        </ul>
        <!--切换页面-->
        <div class="switch_page">
            <div class="chose_page_button" style="float:left;margin-right:30px" v-if="this.$store.state.currentPage != 1" @click="lastPageClicked"><p>← 离现在近点儿的</p></div>
            <p style="float:left;margin-right:30px;">{{this.$store.state.currentPage}} / {{this.$store.state.currentPageCount}}</p>
            <div class="chose_page_button" v-if="this.$store.state.currentPage != this.$store.state.currentPageCount" @click="nextPageClicked"><p>离现在远点儿的 →</p></div>
        </div>
    </div>
</template>


<script>
export default {
    name: 'albumArticles',
    data() {
        return {

        }
    },
    computed: {
        articleListLiStyle: function () {
            return {
                'border-bottom': '1px solid rgb(200, 200, 200)',
            };
        },
        headerTitleStyle: function () {
            if (this.$store.state.phone || this.$store.state.screenWidth < 500) {
                return {
                    'font-size': '20px',
                }
            }
            return {
                'font-size': '30px',
            }
        }
    },
    methods: {
        // 跳转到对应的文集
        jumpToAlbum: function (articleItem) {
            this.$router.push('/home/album/' + articleItem.albumIndex);
        },
        // 上一页
        lastPageClicked: function () {
            this.$store.commit('lastPage');
        },
        // 下一页
        nextPageClicked: function () {
            this.$store.commit('nextPage');
        }
    }
}
</script>

<style>
.articlesList {
    margin-top: 50px;
    margin-bottom: 50px;
}
li {
    list-style-type:none;
    width:100%;
    word-break:break-all;
}
.chose_page_button {
    border-top: 1px solid rgb(150, 150, 150);
    border-bottom: 1px solid rgb(150, 150, 150); 
    text-align: center;
    align-items: center;
    align-content: center;
    /*border-radius: 15px;*/
    width: 110px;
    height: 30px;
    float:left;
    padding: 0;
    margin: 0;
    cursor: pointer;
}
.switch_page {
    padding-top: 30px;
    height: 30px;
    color: rgb(150, 150, 150);
    font-size: 12px;
}
.switch_page p {
    padding:0;
    margin:0;
    padding-top:7px;
}
</style>


