import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        listData : require('./listData'),
        currentAlbum: null, // 当前展示的文章所属的文集
        currentArticle: null, // 当前展示的文章
        currentPage: 1,
        currentPageCount: Math.ceil(require('./listData').articles.length/10) ,
        currentPageArticles: require('./listData').articles.slice(0,9),

        screenWidth: document.documentElement.clientWidth,
        screenHeight: document.documentElement.clientHeight,

        phone: false,
    },
    mutations: {
        // 改变窗口大小
        changeScreenSize(state) {
            state.screenHeight = document.documentElement.clientHeight;
            state.screenWidth = document.documentElement.clientWidth;
        },
        // 设置手机／电脑标示
        setupUserAgent(state, phone) {
            state.phone = phone;
        },
        // 设置当前选中的文集
        setCurrentAlbum(state, album) {
            state.currentAlbum = album;
        },
        clearCurrentAlbum(state) {
            state.currentAlbum = null;
        },
        // 设置当前选中的文章
        setCurrentArticle(state, article) {
            state.currentArticle = article;
        },
        // 上一页
        lastPage(state) {
            state.currentPage--;
            if (state.currentAlbum) {
                state.currentPageArticles = state.currentAlbum.articles.slice((state.currentPage-1)*10,(state.currentPage-1)*10 + 9);
            }else {
                state.currentPageArticles = state.listData.articles.slice((state.currentPage-1)*10,(state.currentPage-1)*10 + 9);
            }
        },
        // 下一页
        nextPage(state) {
            if (state.currentAlbum) {
                state.currentPageArticles = state.currentAlbum.articles.slice((state.currentPage)*10,(state.currentPage-1)*10 + 9);
            }else {
                state.currentPageArticles = state.listData.articles.slice(state.currentPage*10,state.currentPage*10 + 9);
            }
            
            state.currentPage++;
        },
        clearCurrentArticle(state) {
            state.currentArticle = null;
        },
        getAlbumArticlesWithAlbumIndex(state, albumIndex) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumIndex == albumIndex) {
                    if (state.listData.blog[i].articles) {
                        return state.listData.blog[i].articles;
                    }else {
                        var tempArray = [];
                        for (var j = 0; j < state.listData.articles.length; j++) {
                            if (state.listData.articles[j].albumIndex == albumIndex) {
                                tempArray.push(state.listData.articles[j]);
                            }
                        }
                        state.listData.blog[i].articles = tempArray;
                        return state.listData.blog[i].articles;
                    }
                }
            }
        },
        getAlbumArticlesWithAlbumName(state, albumName) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumName == albumName) {
                    if (state.listData.blog[i].articles) {
                        return state.listData.blog[i].articles;
                    }else {
                        var tempArray = [];
                        for (var j = 0; j < state.listData.articles.length; j++) {
                            if (state.listData.articles[j].albumName == albumName) {
                                tempArray.push(state.listData.articles[j]);
                            }
                        }
                        state.listData.blog[i].articles = tempArray;
                        return state.listData.blog[i].articles;
                    }
                }
            }
        },
        selectAlbumWithAlbumIndex(state, albumIndex) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumIndex == albumIndex) {
                    state.listData.blog[i].selected = !state.listData.blog[i].selected;
                    break;
                }
            }
        },
        selectAlbumWithAlbumName(state, albumName) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumName == albumName) {
                    state.listData.blog[i].selected = !state.listData.blog[i].selected;
                    break;
                }
            }
        },
        selectArticleWithArticleIndex(state, articleIndex, albumIndex) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumIndex == albumIndex) {
                    if (state.listData.blog[i].articles) {
                        for (var j = 0; j < state.listData.blog[i].articles.length; j++) {
                            if (state.listData.blog[i].articles[j].articleIndex == articleIndex) {
                                state.listData.blog[i].articles[j].selected = !state.listData.blog[i].articles[j].selected;
                                return;
                            }
                        }
                    }
                }
            }
        },
        selectArticleWithArticleName(state, articleName, albumIndex) {
            for (var i=0; i < state.listData.blog.length; i++) {
                if (state.listData.blog[i].albumIndex == albumIndex) {
                    if (state.listData.blog[i].articles) {
                        for (var j = 0; j < state.listData.blog[i].articles.length; j++) {
                            if (state.listData.blog[i].articles[j].articleName == articleName) {
                                state.listData.blog[i].articles[j].selected = !state.listData.blog[i].articles[j].selected;
                                return;
                            }
                        }
                    }
                }
            }
        }
    }
})


