<template>
	<transition name="fade">
		<div class="launch" @click="backgroundClicked" @mousemove="backgroundMouseMove($event)">
			<!--背景图-->
			<div style="position: absolute;width:100%;height:100%;overflow: hidden;">
				<img src="../assets/launch_background.jpeg" id="backgroundImage" class="blur" :style="backgroundImageClass" style="">
			</div>
			<!--<div class="blur"></div>-->
			<!--中间内容-->
			<div style="position: absolute;width:100%;text-align:center;align-items:center;top:50%;margin-top:-200px;min-width:300px">
				<!--头像-->
				<div class="avatar_border">
					<div class="avatar_view">
						<img src="../assets/avatar.jpeg">
					</div>
				</div>
				<!--名字和个人说明-->
				<div>
					<p style="color:white;font-size: 25px;">林佳</p>
					<p style="color:rgb(220,220,220);font-size: 18px;">放不下设计心的程序狗</p>
				</div>
				<div :style="longLineStyle" style="height:3px;background-color: white;left:50%;position: relative;"></div>
				<div :style="statusContentStyle">
					<p style="color:white;">iOS开发工程师，Web开发工程师，Web研发部门主管</p>
					<p style="color:white;">最近闲赋在家，看看开源项目，调整调整心情，准备修仙<img src="../assets/funny.png" style="width:20px;height:20px;align:bottom"></p>
				</div>
				<div style="width:300px;height:3px;background-color: white;left:50%;margin-left:-150px;position: relative;"></div>
				<!--其他跳转信息-->
				<div class="my_info_view">
					<ul>
						<!--Github-->
						<li>
							<a href="https://github.com/ShmilyLin" target="_blank" v-on:click.stop><img src="../assets/github.png"></a>
						</li>
						<!--简书-->
						<li>
							<a href="http://www.jianshu.com/u/4a549ca9b701" target="_blank" v-on:click.stop><img src="../assets/jianshu.png"></a>
						</li>
						<!--E-mail-->
						<li>
							<a href="Mailto:522402543@qq.com" target="_blank" v-on:click.stop><img src="../assets/email.png"></a>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	export default {
	    name: 'launch', // 名字
		data() { // 数据
			var imageHeight = 1440;// 1800;
			var imageWidth = 2304;// 2880;
			var tempHeight = (window.innerHeight - imageHeight)/2;
			var tempWidth = (window.innerWidth - imageWidth)/2;
			var userAgentInfo = navigator.userAgent;
			var Agents = ["Android", "iPhone",
						"SymbianOS", "Windows Phone",
						"iPad", "iPod"];
			for (var v = 0; v < Agents.length; v++) {
				if (userAgentInfo.indexOf(Agents[v]) > 0) {
					tempWidth = -200;
					tempHeight = 0;
					break;
				}
			}
	        return {
				animating: false,
				imageHeight: imageHeight,
				imageWidth: imageWidth,
				backgroundImageClass: {
					'margin-left': tempHeight + 'px',
					'margin-top': tempWidth + 'px',
				},
			}
		},
		watch: { // 监听 数据 改变
			animating: function (val) {
				console.log("animating值为", this.animating);
				console.log("监听到的值为", val);
			}
		},
		computed: {
			longLineStyle: function () {
				return {
					'width': (this.$store.state.screenWidth<500?300:500) + 'px',
					'margin-left': (this.$store.state.screenWidth<500?-150:-250) + 'px'
				}
			},
			statusContentStyle: function () {
				if (this.$store.state.screenWidth<500) {
					return {
						'left': '50%',
						'width': '300px',
						'margin-left': '-150px',
						'position': 'relative'
					}
				}
				return {}
			}
		},
		methods: { // 方法，不带缓存
			backgroundClicked: function (event) {
				this.$router.push('home');
			},
			backgroundMouseMove: function (event) {
				if (event.clientX == window.innerWidth/2 && event.clientY == window.innerHeight/2) {
					return;
				}
				if (this.imageWidth <= window.innerWidth && this.imageHeight <= window.innerHeight) {
					return;
				}
				var tempImageWidth = this.imageWidth - window.innerWidth;
				var tempImageheight = this.imageHeight - window.innerHeight;
				var distanceLeft = 0;
				var distanceTop = 0;
				if (event.clientX < window.innerWidth/2) {
					distanceLeft = (event.clientX * tempImageWidth/2)/(window.innerWidth/2);
				}else {
					distanceLeft = (((event.clientX - window.innerWidth/2)* tempImageWidth/2)/(window.innerWidth/2)) + tempImageWidth/2;
				}
				if (event.clientY < window.innerHeight/2) {
					distanceTop = (event.clientY * tempImageheight/2)/(window.innerHeight/2);
				}else {
					distanceTop = (((event.clientY - window.innerHeight/2)* tempImageheight/2)/(window.innerHeight/2)) + tempImageheight/2;
				}
				this.backgroundImageClass = {
					'margin-left': "-" + distanceLeft + "px",
					'margin-top': "-" + distanceTop + "px",
				}
			},
			launchClicked:function (event) {
				alert($("#test").text());
				alert(this.count);
			}
		},
		mounted() {
			
		}
	}
</script>

<style scoped>
.launch {
	width: 100%;
	height: 100%;
	position: absolute;
	background-color: black;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1.0s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}

.blur {	
	filter:blur(10px);
	-webkit-filter:blur(10px);
	-moz-filter:blur(10px);
	-ms-filter:blur(10px);
	-o-filter:blur(10px);
}

.avatar_border {
	position: relative;
	width:106px;
	height:106px;
	border-radius:53px;
	border:1px solid white;
	background-color:white;
	left: 50%;
	margin-left: -53px;
	
}
.avatar_view {
	width:100px;
	height:100px;
	border-radius:50px;
	border:1px solid white;
	overflow:hidden;
	margin-left:2px;
	margin-top:2px;
}
.avatar_view img {
	width:100%;
	height:100%;
}
.my_info_view {
	width:300px;
	position: relative;
	left: 50%;
	margin-left: -150px;
	overflow: hidden;
	margin-top: 30px;
}

.my_info_view ul {
	margin: 0;
	padding: 0;
}
.my_info_view li {
	float: left;
	width: 33%;
	height: 20px;
	overflow: hidden;
	list-style-type:none;
}

.my_info_view li img {
	width:20px;
	height:20px;
}
</style>



